<template>
	<div id="meetingSchedule">
		<h2 class="title_comp">미팅일정</h2>

		<div class="loc_box_wrap" v-if="isNoData">
			<div
				class="loc_box"
				v-for="location in $store.state.arrMeetingSchedule"
				:key="location.Task.T_IDX"
			>
				<div class="title">
					<font-awesome-icon
						icon="fa-solid fa-location-dot"
					></font-awesome-icon>
					{{ location.Task.T_TITLE }}
				</div>
				<div class="users">
					<div
						class="user"
						v-for="worker in location.Workers"
						:key="worker.TW_IDX"
					>
						<div class="user_img">
							<img
								:src="`${profileImgUrl}${worker.TW_ID}`"
								:alt="`${worker.TW_NAME}님의 이미지`"
							/>
						</div>
						<div class="name">{{ worker.TW_NAME }}</div>
					</div>
					<div class="time">
						{{ location.Task.T_TARGETDATE.split("T")[1].substr(0, 5) }}
					</div>
				</div>
			</div>
		</div>
		<div v-else>미팅일정이 없습니다.</div>
	</div>
</template>

<script>
import { library } from "@fortawesome/fontawesome-svg-core";
import { faLocationDot } from "@fortawesome/free-solid-svg-icons";
library.add(faLocationDot);
import { fetchMeetingSchedule } from "@/api/index";
export default {
	data() {
		return {
			profileImgUrl: process.env.VUE_APP_IMG_URL,
			isNoData: true,
		};
	},
	async created() {
		try {
			const { data } = await fetchMeetingSchedule();
			this.$store.commit("setMeetingSchedule", data);

			if (data.length == 0) {
				this.isNoData = false;
			}
		} catch (error) {
			// console.log(error.response);
		}
	},
};
</script>

