<template>
	<div id="ezstory">
		<h2 class="title_comp">이지스토리</h2>
		<swiper
			ref="ezstory_swipe"
			class="ezstory_swipe"
			:options="swiperOption"
			@slideChange="onSlideChange"
		>
			<swiper-slide v-for="list in $store.state.arrEzStoryList" :key="list.IDX">
				<a :href="list.LinkUrl" target="_blank">
					<div class="ezstory_img">
						<img :src="list.ThumbFileUrl" alt="" />
					</div>
					<div class="ezstory_tit">{{ list.TITLE }}</div>
				</a>
			</swiper-slide>
		</swiper>
		<div class="swiper-pagination" slot="pagination"></div>
	</div>
</template>

<script>
import { fetchEzStory } from "@/api/index";
import { EzStorySwipe } from "@/mixins/mixins";
export default {
	mixins: [EzStorySwipe],
	data() {
		return {
			arrEzStoryList: [],
		};
	},
	async created() {
		try {
			const { data } = await fetchEzStory();
			this.$store.commit("setEzStoryList", data);
		} catch (error) {
			// console.log(error.response);
		}
	},
};
</script>

