<template>
	<div id="meetingRoom">
		<h2 class="title_comp">회의실</h2>
		<div class="room_condition">
			<div class="condition">
				<span class="txt">총 예약</span>
				<span class="num">{{ $store.state.arrMeetingRoom.length }}</span>
			</div>
			<div class="condition">
				<span class="txt">진행 중</span>
				<span class="num">{{ arrStatusUnderway.length }}</span>
			</div>
			<div class="condition">
				<span class="txt">예정</span>
				<span class="num">{{ arrStatusSchedule.length }}</span>
			</div>
			<div class="condition">
				<span class="txt">종료</span>
				<span class="num">{{ arrStatusEnd.length }}</span>
			</div>
		</div>
		<ul class="room_list">
			<li
				v-for="(list, index) in $store.state.arrMeetingRoom"
				:key="list.SIdx"
				class="list"
			>
				<div class="room_name">{{ list.RoomName }}</div>
				<div class="progress_bar">
					<span class="running_time">{{
						`${arrStartTime[index]}
            ~ 
            ${arrEndTime[index]}`
					}}</span>
					<div
						class="bar_box"
						:class="{
							completed: list.Status == -1,
							notYet: list.Status == 1 || list.Status == 2,
						}"
					>
						<span class="bar"></span>
					</div>
				</div>
				<div class="user">
					<div class="img_area">
						<img
							:src="`${profileImgUrl}${list.UserId}`"
							:alt="`${list.UserName}님의 이미지`"
						/>
					</div>
					<div class="name">{{ list.UserName }}</div>
				</div>
			</li>
		</ul>
		{{ getWidth }}
	</div>
</template>

<script>
import { fetchMeetingRoom } from "@/api/index";
import moment from "moment";
export default {
	data() {
		return {
			profileImgUrl: process.env.VUE_APP_IMG_URL,
			currentHour: "", // 현재시간 (hh:mm)
			currentMin: "", // 현재시간 (hh:mm)
			arrStatusEnd: [], // 종료된 회의 수
			arrStatusUnderway: [], // 진행중 회의 수
			arrStatusSchedule: [], // 예정된 회의 수
			arrStartTime: [],
			arrEndTime: [],
		};
	},
	computed: {
		getWidth() {
			this.$store.state.arrMeetingRoom.map(el => {
				// console.log(el);
				if (
					parseInt(`${this.currentHour}${this.currentMin}`) >=
						parseInt(
							el.Startdate.split("T")[1].substr(0, 5).split(":").join(""),
						) &&
					parseInt(`${this.currentHour}${this.currentMin}`) <=
						parseInt(el.Enddate.split("T")[1].substr(0, 5).split(":").join(""))
				) {
					// console.log(el);
					console.log(
						parseInt(
							el.Enddate.split("T")[1].substr(0, 5).split(":").join(""),
						) -
							parseInt(
								el.Startdate.split("T")[1].substr(0, 5).split(":").join(""),
							),
					);
				}
			});
			// this.arrStartTime.map(el => {
			// 	if (
			// 		parseInt(`${this.currentHour}${this.currentMin}`) >
			// 		parseInt(el.split(":").join(""))
			// 	) {
			// 		console.log(el);
			// 	}
			// 	// console.log(
			// 	// 	parseInt(
			// 	// 		`${this.currentHour}${this.currentMin}` - el.split(":").join(""),
			// 	// 	),
			// 	// );
			// });
			return "90%";
		},
	},
	methods: {
		async fetchMtRoom() {
			try {
				const response = await fetchMeetingRoom();
				this.$store.commit("setMeetingRoom", response.data);
				this.currentTime = `${moment().hour()}${moment().minute()}`;

				this.arrStatusEnd = response.data.filter(number => number.Status == -1);
				this.arrStatusUnderway = response.data.filter(
					number => number.Status == 0,
				);
				this.arrStatusSchedule = response.data.filter(
					number => number.Status == 1 || number.Status == 2,
				);
				this.arrStartTime = response.data.map(val =>
					val.Startdate.split("T")[1].substr(0, 5),
				);
				this.arrEndTime = response.data.map(val =>
					val.Enddate.split("T")[1].substr(0, 5),
				);

				this.currentHour = moment().format("HH");
				this.currentMin = moment().format("mm");
			} catch (error) {
				// console.log(error.response);
			}
		},
	},
	created() {
		this.fetchMtRoom();
	},
};
</script>

