<template>
	<div id="anniverse">
		<h2 class="title_comp">기념일</h2>
		<div class="tab_area">
			<div class="tab_wrap">
				<button
					class="tab"
					type="button"
					v-for="tab in tabs"
					:key="tab"
					:class="{ active: tab === selectedTab }"
					@click="onClickTab(tab)"
				>
					{{ tab }}
				</button>
			</div>
			<div class="btn" @click="showModal = true">더보기</div>
		</div>
		<div class="container">
			<template v-if="selectedTab === tabs[0]">
				<div class="tab_con">
					<swiper
						ref="ezDay-swiper"
						class="ezDay_swiper"
						:options="swiperOption"
					>
						<swiper-slide
							v-for="item in $store.state.arrEzdayList"
							:key="item.E_IDX"
						>
							<router-link :to="`/User/${item.E_IDX}`">
								<div class="user_img">
									<img :src="`${baseImageUrl}${item.EMAIL}`" alt="" />
								</div>
								<div class="user_name">{{ item.NAME }}</div>
								<div class="work_year">{{ item.easyday }}</div>
							</router-link>
						</swiper-slide>
					</swiper>
				</div>
			</template>
			<template v-else>
				<div class="tab_con">
					<swiper
						ref="birthday_swiper"
						class="birthday_swiper"
						:options="swiperOption"
					>
						<swiper-slide
							v-for="item in $store.state.arrBirthdayList"
							:key="item.E_IDX"
						>
							<router-link :to="`/User/${item.E_IDX}`">
								<div class="user_img">
									<img :src="`${baseImageUrl}${item.EMAIL}`" alt="" />
								</div>
								<div class="user_name">{{ item.NAME }}</div>
								<div class="icon_birthday">
									{{ item.EI_BIRTH_DATE | formatFromMonth }}
									<!-- <font-awesome-icon icon="fa-solid fa-gift" /> -->
								</div>
							</router-link>
						</swiper-slide>
					</swiper>
				</div>
			</template>
		</div>
		<!-- use the modal component, pass in the prop -->
		<Modal v-if="showModal" @close="showModal = false">
			<div slot="header">기념일</div>
			<div slot="body" class="list_area">
				<div class="list_wrap ezDay">
					<div class="sub_title">EASYDAY</div>
					<ul class="ezday_list">
						<li class="list" v-for="item in arrEzday" :key="item.E_IDX">
							<div class="date">{{ item.date }}</div>
							<div class="user_wrap">
								<div
									class="user"
									v-for="(user, index) in item.user"
									:key="index"
								>
									<div class="img">
										<img :src="`${baseImageUrl}${user.EMAIL}`" alt="" />
									</div>
									<div class="user_info">
										<div class="name">
											{{ user.NAME }}
										</div>
										<div class="call_name">
											{{ user.CALL_NAME }}
										</div>
									</div>
								</div>
							</div>
						</li>
					</ul>
				</div>
				<div class="list_wrap birthday">
					<div class="sub_title">BIRTHDAY</div>
					<ul class="ezday_list">
						<li class="list" v-for="item in arrBirthday" :key="item.E_IDX">
							<div class="date">{{ item.date }}</div>
							<div class="user_wrap">
								<div
									class="user"
									v-for="(user, index) in item.user"
									:key="index"
								>
									<div class="img">
										<img :src="`${baseImageUrl}${user.EMAIL}`" alt="" />
									</div>
									<div class="user_info">
										<div class="name">
											{{ user.NAME }}
										</div>
										<div class="call_name">
											{{ user.CALL_NAME }}
										</div>
									</div>
								</div>
							</div>
						</li>
					</ul>
				</div>
			</div>
			<div slot="footer" class="footer"></div>
		</Modal>
	</div>
</template>

<script>
import Modal from "@/components/common/ModalPopup.vue";
import { fetchEzDay, fetchBirthday } from "@/api/index";
// import { EzAnniverseSwipe, ezBirthdaySwipe } from "@/mixins/mixins";
import { library } from "@fortawesome/fontawesome-svg-core";
import { faGift, faCircleXmark } from "@fortawesome/free-solid-svg-icons";
import "swiper/dist/css/swiper.css";
import { swiper, swiperSlide } from "vue-awesome-swiper";
library.add(faGift, faCircleXmark);

export default {
	// mixins: [EzAnniverseSwipe, ezBirthdaySwipe],
	components: {
		Modal,
		swiper,
		swiperSlide,
	},
	data() {
		return {
			baseImageUrl: process.env.VUE_APP_IMG_URL,
			tabs: ["이지데이", "생일자"],
			selectedTab: 0,
			showModal: false,
			arrEzday: [],
			arrBirthday: [],
			swiperOption: {
				slidesPerView: "auto",
				spaceBetween: 10,
			},
			showSecondSwiper: false,
		};
	},
	methods: {
		onClickTab(tab) {
			this.selectedTab = tab;
			this.showSecondSwiper = true;
		},
		printEzday() {
			const arr = [];
			let uniqueArr = [];
			let filterdArrAnni = [];
			for (let i = 0; i < this.getEzday.length; i++) {
				arr.push(
					this.$options.filters.formatDate(this.getEzday[i].ENTERING_DATE),
				);
				uniqueArr = arr.filter((element, index) => {
					return arr.indexOf(element) === index;
				});
			}
			for (let i = 0; i < uniqueArr.length; i++) {
				for (let j = 0; j < this.getEzday.length; j++) {
					if (
						uniqueArr[i] ===
						this.$options.filters.formatDate(this.getEzday[j].ENTERING_DATE)
					) {
						filterdArrAnni.push({ date: uniqueArr[j], user: [] });
						filterdArrAnni[i].user.push(this.getEzday[j]);
					}
				}
			}
			for (let i = 0; i < filterdArrAnni.length; i++) {
				if (filterdArrAnni[i].date === undefined) {
					filterdArrAnni.splice(i, 1);
					i--;
				}
			}
			this.arrEzday = filterdArrAnni;
		},
		printBirthday() {
			const arr = [];
			let uniqueArr = [];
			let filterdArrAnni = [];
			for (let i = 0; i < this.getBirthday.length; i++) {
				arr.push(
					this.$options.filters.formatDate(this.getBirthday[i].EI_BIRTH_DATE),
				);
				uniqueArr = arr.filter((element, index) => {
					return arr.indexOf(element) === index;
				});
			}
			for (let i = 0; i < uniqueArr.length; i++) {
				for (let j = 0; j < this.getBirthday.length; j++) {
					if (uniqueArr[i] === arr[j]) {
						filterdArrAnni.push({ date: uniqueArr[j], user: [] });
						filterdArrAnni[i].user.push(this.getBirthday[j]);
					}
				}
			}
			for (let i = 0; i < filterdArrAnni.length; i++) {
				if (filterdArrAnni[i].date === undefined) {
					filterdArrAnni.splice(i, 1);
					i--;
				}
			}
			this.arrBirthday = filterdArrAnni;
		},
	},
	computed: {
		getEzday() {
			return this.$store.state.arrEzdayList;
		},
		getBirthday() {
			return this.$store.state.arrBirthdayList;
		},
	},
	filters: {
		formatDate(value) {
			const date = new Date(value);
			let month = date.getMonth() + 1;
			month = month > 9 ? month : `0${month}`;
			let day = date.getDate();
			day = day > 9 ? day : `0${day}`;
			return `${month}월 ${day}일`;
		},
	},
	async created() {
		this.selectedTab = this.tabs[0];
		try {
			const ezDay = await fetchEzDay();
			const birthday = await fetchBirthday();
			this.$store.commit("setEzDayList", ezDay.data);
			this.$store.commit("setBirthdayList", birthday.data);
			this.printEzday();
			this.printBirthday();
		} catch (error) {
			// console.log(error.response);
		}
	},
};
</script>

