<template>
	<div id="boardList">
		<div class="board_tab">
			<swiper :options="swiperOption" ref="boardTab">
				<swiper-slide v-for="(btn, index) in arrTabList" :key="btn.BBSCATEID">
					<a href="javascript:" @click="fetchSortData(btn.CATEID, index)">{{
						btn.BBSNAME
					}}</a>
				</swiper-slide>
			</swiper>
		</div>
		<div class="board_tab_content">
			<a
				@click="getBoardListData(index)"
				class="list"
				v-for="(list, index) in $store.state.arrBoardList"
				:key="list.BBSDATAID"
				:href="`https://easymedia.matty.works:8443/_${list.USERID}/${list.BBSDATAID}`"
				target="_blank"
			>
				<div class="user_info">
					<div class="user_img">
						<img :src="`${baseImageUrl}${list.USERID}`" alt="" />
					</div>
					<div class="user_name">{{ list.USERNAME }}</div>
				</div>
				<div class="text_info">
					<div class="title">{{ list.TITLE }}</div>
					<div class="e_area">
						<div class="recmd">
							<font-awesome-icon icon="fa-solid fa-heart" />{{ list.CNT_RECMD }}
						</div>
						<div class="read">
							<font-awesome-icon icon="fa-regular fa-eye" />{{ list.CNT_READ }}
						</div>
						<div class="date">
							{{
								list.COMMENTUPDATE == null
									? ""
									: list.COMMENTUPDATE.substr(0, 10)
							}}
						</div>
					</div>
				</div>
			</a>
		</div>
	</div>
</template>

<script>
import { fetchBoardList, fetchCommnunityCategory } from "@/api/index";
import { SwiperTabBtn } from "@/mixins/mixins";
import { library } from "@fortawesome/fontawesome-svg-core";
import { faHeart } from "@fortawesome/free-solid-svg-icons";
import { faEye } from "@fortawesome/free-regular-svg-icons";
library.add(faHeart, faEye);

export default {
	mixins: [SwiperTabBtn],
	// components: {
	// 	Modal,
	// },
	data() {
		return {
			baseImageUrl: process.env.VUE_APP_IMG_URL,
			arrTabList: [], //공지사항, 자유게시판, 피플, FAQ, 프로젝트, 이벤트, 자기계발, 정보, 취미, 일상
			currentSelectNum: 0,
			itemIdx: {},
		};
	},
	methods: {
		getBoardListData(index) {
			this.showModal = !this.showModal;
			this.itemIdx = this.$store.state.arrBoardList[index];
			// console.log(this.itemIdx);
		},
		async fetchSortData(category, index) {
			try {
				const response = await fetchBoardList(category);
				this.$store.commit("setBoardList", response.data);

				// 버튼 active
				this.$refs.boardTab.swiper.slideTo(index);
				this.$refs.boardTab.$children[
					this.currentSelectNum
				].$el.classList.remove("swiper-slide-select");
				this.currentSelectNum = index;
				this.$refs.boardTab.$children[this.currentSelectNum].$el.classList.add(
					"swiper-slide-select",
				);
			} catch (error) {
				// console.log(error.response);
			}
		},
		async fetchCategory() {
			try {
				const { data } = await fetchCommnunityCategory();
				this.arrTabList = data;

				setTimeout(() => {
					this.$refs.boardTab.$children[
						this.currentSelectNum
					].$el.classList.add("swiper-slide-select");
				}, 50);
			} catch (error) {
				// console.log(error.response);
			}
		},
	},
	created() {
		this.fetchSortData("NOTICE", 1);
		this.fetchCategory();
	},
	// computed: {
	// 	modalCtrl() {
	// 		return this.showModal != this.showModal;
	// 	},
	// },
	mounted() {
		// this.$refs.boardTab.$children[this.currentSelectNum].$el.classList.add(
		// 	"swiper-slide-select",
		// );
	},
};
</script>

